import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import {
	mapState
} from 'vuex';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			showList: [],
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '未支付',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待确认',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '已确认',
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},

	onLoad(options) {
		uni.request({
			url: this.api + '/api/findOrderList',
			data: {
				token: this.userInfo.token,
				showStatus: '',
				pageNo: '1',
				pageSize: '15'
			},
			success: (res) => {
				this.$api.msg(res.data.msg)
				if (res.data.code == 1) {
					var dataList = res.data.list
					dataList.map((val, key) => {
						if (val.showStatus === '001') {
							val.Status = '未支付'
							this.navList[1].orderList.push(val)
						} else if (val.showStatus === '002') {
							val.Status = '待确认'
							this.navList[2].orderList.push(val)
						} else if (val.showStatus === '003') {
							val.Status = '已确认'
							this.navList[3].orderList.push(val)
						}
						// else{
						this.navList[0].orderList.push(val)
						// }
					})
					console.log(this.navList)
				}
			}
		});
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题 
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = +options.state;




		if (options.state == 0) {
			this.loadData()
		}

	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		//获取订单列表
		loadData(source) {

			// console.log(source)
		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip,
					stateTipColor
				})

				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val => val.id === item.id);
				index !== -1 && list.splice(index, 1);

				uni.hideLoading();
			}, 600)
		},
		//点击订单，跳转详情
		tapDetail(item) {
			// console.log(item)
			// var params = JSON.stringify(item)
			let id = item;
			id = JSON.stringify(id)
			uni.navigateTo({
				url: `../order/orderDetail?postid=${id}`
			});
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待发货';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
